import React from "react";
import { Endpoint } from "types";
import { useParams } from "react-router-dom";
import { CopyOutlined } from "@ant-design/icons";
import { Space, Button, message, Alert, Card, Tooltip } from "antd";
import RoleGuard from "../../standalone/role-guard";
import queryClient from "components/utils/queryClient";
import useClipboard from "use-clipboard-hook";

export default function PowerShellCommandCard() {
  const { id } = useParams();
  const endpoint: Endpoint = queryClient.getQueryData(`/endpoint/${id}`);

  const { copy: copyPSCommand } = useClipboard({
    onSuccess: (text) => message.info(`PowerShell command copied.`),
  });

  return (
    <Alert.ErrorBoundary>
      <Card
        title="PowerShell"
        bordered={false}
        style={{ minWidth: "100%" }}
        extra={
          <Space size="small">
            <RoleGuard requiredRoles={["Operator", "Administrator"]}>
              <Tooltip title="Copy">
                <Button
                  type="text"
                  icon={<CopyOutlined />}
                  onClick={() =>
                    copyPSCommand(
                      `Invoke-RestMethod ${
                        endpoint?.fullUrl ||
                        `${window.location.origin}${endpoint?.url}`
                      }`
                    )
                  }
                />
              </Tooltip>
            </RoleGuard>
          </Space>
        }
        headStyle={{
          borderBottom: "unset",
        }}
      >
        {`Invoke-RestMethod ${
          endpoint?.fullUrl || `${window.location.origin}${endpoint?.url}`
        }`}
      </Card>
    </Alert.ErrorBoundary>
  );
}
